export default{
  data() {
    return {
      title: '',
      type: '',
      showDel: false,
      code_word: '获取验证码',
      username: '',
      code: '',
      check_code: false,
      showInfo: false,
      showTitle: '请获取手机验证码'
    }
  },
  onLoad(option) {
    this.type = option.type
    if(option.type === 'register') {
      this.title = '用户注册'
      return
    }
    if(option.type === 'forget') {
      this.title = '找回密码'
      return
    }
  },
  onShow() {
    this.showInfo = false
  },
  computed: {
    windowHeight() {
      return this.$store.state.windowHeight
    },
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    onInput(e) {
      let value = e.detail.value
      if(value) {
        this.showDel = true
        return
      }
      this.showDel = false
    },
    goBind() {
      uni.navigateTo({
        url: './bindTel'
      })
    },
    goNext() {
      let that= this
      console.log(that.check_code, that.code, that.showInfo)
      if(!that.username) {
        uni.showToast({
          title: '请输入手机号码',
          icon: 'none'
        })
        return
      }
      if(that.check_code) {
        if(that.code.length === 4) {
          that.$http({
            url: that.$api.smscodeyz,
            data: {
              mobile: that. username,
              code: that.code
            },
            cb: (err, res) => {
              if(!err && res.code === 1) {
                uni.navigateTo({
                  url: 'password?type=' + that.type + '&mobile=' + that.username
                })
              } else if(res.code === 0) {
                uni.showToast({
                  title: res.msg,
                  icon: 'none'
                })
              } else {
                uni.showToast({
                  title: '验证码验证失败',
                  icon: 'none'
                })
              }
            }
          })  
        } else {
          uni.showToast({
            title: '验证码格式错误',
            icon: 'none'
          })
        }  
      } else {
        uni.showToast({
          title: that.showTitle,
          icon: 'none'
        })
      }
    },
    goLogin() {
      uni.navigateTo({
        url: 'login'
      })
    },
    // 获取手机验证码
    getCode() {
      let that = this
      console.log(that.username)
      let value = /^1[3456789]\d{9}$/.test(that.username)
      
      if (!value){
        uni.showToast({
          title: '请输入正确的手机号码',
          icon: 'none'
        })
        return
      }
      this.$http({
        url: this.$api.sendcode,
        method: 'POST',
        data: {
          mobile: this.username
        },
        cb: (err, res) => {
          if(!err && res.code === 1) {
            that.check_code = true
            that.code_word = 120
            that.showInfo = true
            that.showTitle = '验证码过期，请重新获取'
            setInterval(function() {
              if (that.code_word > 0) {
                that.code_word--
              }
              if (that.code_word === 0) {
                that.check_code = false
                that.showInfo = false
                return
              }
            }.bind(that), 1000)
          } else {
            uni.showToast({
              title: '手机验证码获取失败',
              icon: 'none'
            })
          }
        }
      })
    }
  }
}
